import React, { useEffect, useState } from 'react';
import { useParams } from 'react-router';
import SojiBaseInit from '@modules/SojiBase';
import PagePreloader from '@components/preloader/PagePreloader';
import Contents from './components/Contents';
import Results from './components/Results';
import Reports from './components/Reports';

import './index.css';

export default function PredictionResults() {
    const { uniqueId } = useParams();
    const [state, setState] = useState({
        fetchedData: false,
    });

    const stateHandler = (key, value) => {
        state[key] = value;
        setState({ ...state });
    };

    useEffect(() => {
        (async () => {
            const SojiBase = await SojiBaseInit;
            const getter = await SojiBase.get(`v1/suspected_users/get-prediction-results/${uniqueId}`);
            getter.on('static', async (response) => {
                const data = response.response.data[0];
                stateHandler('fetchedData', data);
            });
        })();
    }, []);

    return (
        <div className="container content_Container">
            {state.fetchedData ? (
                <div className="row content_wrapper">
                    <div className="col-md-5">
                        <Contents data={state.fetchedData} />
                    </div>
                    <div className="col-md-4">
                        <Results data={state.fetchedData} />
                    </div>
                    <div className="col-md-3">
                        <Reports data={state.fetchedData} />
                    </div>
                </div>
            ) : (
                <PagePreloader />
            )}
        </div>
    );
}
