import React, { useState } from 'react';
import ModalPage from '@components/layouts/ModalPage';
import './index.css';

export default function SenderDetails({ onRequestClose }) {
    const [getIsLoading, setIsLoading] = useState(false);
    const [error, setError] = useState();

    const actionButton = async () => {
        return;
    };

    return (
        <ModalPage
            isPageLoaded
            pageTitle="Enter Your Statement"
            buttonTitle="Report Suspected"
            isButtonLoading={getIsLoading}
            actionButton={actionButton}
            onRequestClose={onRequestClose}
            error={error}
            className="sender_details"
        >
            <div className="report_modal">
                <textarea className="modal_textbox"></textarea>
            </div>
        </ModalPage>
    );
}
