import React, { useState } from 'react';
import { Input } from '@components/libs/Forms';
import Modal from '@modules/Modal';
import ReportModal from './ReportModal';
import './index.css';

export default function Reports({ data }) {
    const [viewModal, setModal] = useState(false);
    const actionButton = () => setModal(!viewModal);
    return (
        <>
            <div className="reporting_box">
                <Input value={data.name} title="Full Name" />
                <Input value={data.email} title="Email Address" />
                <Input value={data.phoneNumber} title="Phone Number" />
                <Input value={data.scammedAmount} title="Scammed Amount" />
                <button className="report_button" onClick={actionButton}>
                    Report
                </button>
                <button className="report_share_button">Share</button>
            </div>
            <Modal visible={viewModal} onRequestClose={() => setModal(false)}>
                <ReportModal onRequestClose={() => setModal(false)} />
            </Modal>
        </>
    );
}
