import React from 'react';
import './index.css';

export default function Ratings({data}) {
    return (
        <div className="rating_box">
            <p>
                Your content matches <span style={{color: '#FF6600', fontWeight: 'bold', fontSize: 18}}>20</span> fradualent documents.
            </p>
            <div className='rating_percentage_box'>
                <p>Percentage Match:</p>
                <p className='percentage_text'>{parseInt(data.prediction).toFixed(2)}%</p>
            </div>
        </div>
    );
}
